





























































































.md-button {
	margin: 0 8px;
}

.pagination {
	display: block;
	width: 100%;
	margin-top: 2rem;
	text-align: center;
}

#page-number {
	height: 40px;
	padding: 0;
	margin: 0;
	input {
		width: 100%;
		max-width: 88px;
		height: 40px;
		padding: 0;
		margin: 0;
		font-size: 1.2em;
		font-weight: bold;
		line-height: 1em;
		color: inherit;
		text-align: center;
		background: transparent;
		border: none;
		outline: none;
		&::-webkit-inner-spin-button,
		&::-webkit-outer-spin-button {
			margin: 0;
			-webkit-appearance: none;
			-moz-appearance: none;
			appearance: none;
		}
	}
}
