





























































































































































































































































.wrapper {
	margin: 30px 5% 0 5%;
}

.items-per-page-picker {
	float: right;
	max-width: 200px;
	margin: 0 8px;
}

.card-wrapper {
	box-sizing: border-box;
	padding: 5px;
}

table {
	width: 100%;
}

.flex {
	display: flex;
	align-items: center;
	justify-content: flex-end;
	margin: 1em 0;
}

.head {
	display: flex;
	flex-direction: column;
}

.empty_state {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	max-width: 420px;
	padding: 36px;
	margin: 0 auto;
	text-align: center;

	img {
		width: 150px;
	}

	strong {
		font-size: 26px;
		font-weight: 500;
		line-height: 40px;
	}
}

#search-input {
	float: left;
	margin-top: 16px;
	margin-bottom: 16px;
	font-size: 1.75em !important;
	#search-query-input {
		display: inline-block;
		width: 100%;
		max-width: 500px;
		padding: 0;
		margin: 0;
		margin-left: 5px;
		font-size: 1em;
		line-height: 1em;
		color: inherit;
		background: transparent;
		border: none;
		border-bottom: 1px solid grey;
		outline: none;
		&:focus {
			color: #b10438;
			border-bottom: 1px solid #b10438;
		}
	}
	#result-headline {
		display: block;
		font-size: 1rem;
	}
}
